body {
    font-family: "Poppins", sans-serif;
    background-color: #f3f4f6;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.home-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.container {
    max-width: 400px;
    width: 100%;
}

.card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
}

.title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
    color: #333;
}

.tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.tabs button {
    background: none;
    border: none;
    font-size: 16px;
    margin: 0 10px;
    cursor: pointer;
    padding: 5px 10px;
    color: #777;
    border-bottom: 2px solid transparent;
}

.tabs button.active {
    color: #333;
    border-bottom: 2px solid #007bff;
}

.form-container {
    margin-top: 20px;
}
